body{
    display: flex;
    position: absolute;
    margin: 0;
    padding: 0;
    background-repeat: no-repeat;
    background-size: cover;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.profilewrapper {
    display: flex;
    position: relative;
    flex-direction: column;
    margin: 0;
    padding: 0;
    align-items: center;
    width: 100%;
    height: 100%;
}

.profileWrapperHeader {
    display: flex;
    position: relative;
    flex-direction: row;
    margin: 0;
    padding: 0;
    top: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    background-color: white;
    border-bottom-style: solid;
    border-bottom-color: lightgray;
    border-bottom-width: 0.08cm;
}

.profileWrapperContentHolder {
    display: flex;
    position: relative;
    flex-direction: column;
    margin: 0cm;
    padding: 0;
    gap: 0cm;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
}

.profileInfoHolder {
    display: flex;
    position: relative;
    flex-direction: column;
    margin: 0cm;
    padding: 0;
    gap: 0cm;
    justify-content: space-between;
    align-items: center;
    width: 85%;
    height: auto;
}

.backbuttonHolder {
    display: flex;
    position: absolute;
    left: 0;
    flex-direction: column;
    margin: 0.1cm;
    padding: 0;
    justify-content: center;
    align-items: center;
    width: auto;
    height: auto;
    background-color: white;
    border-radius: 100%;
    padding: 0.3cm;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Box shadow */
}
.backbuttonHolder:hover{
    cursor: pointer;
}

.securityInfoHolder {
    display: flex;
    position: relative;
    flex-direction: column;
    margin: 0cm;
    padding: 0;
    gap: 0cm;
    justify-content: space-between;
    align-items: center;
    width: 85%;
    height: auto;
}

.moreHolder {
    display: flex;
    position: relative;
    flex-direction: column;
    margin: 0cm;
    padding: 0;
    gap: 0cm;
    justify-content: space-between;
    align-items: center;
    width: 85%;
    height: auto;
}


